.top-line {
  background-color: #ffffff;
  border-bottom: 1px solid #ECECEC;
  padding: 50px 0;
  padding-bottom: 49px; }
  @media screen and (max-width: 1200px) {
    .top-line {
      padding-top: 30px;
      padding-bottom: 29px; } }
  @media screen and (max-width: 767px) {
    .top-line {
      padding: 6px 0;
      border-bottom: none; } }
  .top-line .top-line__content {
    display: flex;
    align-items: center; }
    @media screen and (max-width: 767px) {
      .top-line .top-line__content {
        justify-content: center; } }

.burger {
  width: 30px;
  height: 24px;
  display: none; }
  @media screen and (max-width: 767px) {
    .burger {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      margin-right: auto;
      position: relative; } }
  .burger span {
    width: 30px;
    height: 4px;
    background-color: #6F36BC;
    border-radius: 4px;
    transition: all .25s ease; }
  .burger.active span {
    position: absolute;
    top: 10px; }
    .burger.active span:nth-child(1) {
      transform: rotate(45deg); }
    .burger.active span:nth-child(2) {
      transform: translateX(100%);
      opacity: 0; }
    .burger.active span:nth-child(3) {
      transform: rotate(-45deg); }

.logo {
  margin-right: auto; }
  @media screen and (max-width: 767px) {
    .logo {
      width: 48px; }
      .logo img {
        width: 100%;
        height: auto; } }
  .logo img {
    display: block; }

@media screen and (max-width: 767px) {
  .nav-top {
    display: none; } }

.nav-top ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  margin-left: -10px;
  margin-right: -10px; }
  .nav-top ul li {
    padding-left: 10px;
    padding-right: 10px; }
    .nav-top ul li a {
      display: block;
      padding: 0.393em 20px;
      line-height: 1;
      text-decoration: none;
      color: #000;
      font-weight: 600;
      border-radius: 30px;
      font-size: 14px; }
      .nav-top ul li a:hover {
        text-decoration: underline; }
      .nav-top ul li a.active {
        background-color: #FF4778;
        color: #ffffff; }
        .nav-top ul li a.active:hover {
          text-decoration: none;
          opacity: 0.8; }

@media screen and (max-width: 767px) {
  .user {
    display: none; } }

.user .name {
  display: none; }

.user .user__ava {
  display: block;
  width: 40px;
  height: 40px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 2em;
  overflow: hidden;
  transition: all .25s ease; }
  .user .user__ava:hover {
    transform: scale(1.1); }
